.container {
  display: grid;
  grid-template-columns: 150px 1fr;
}

.top {
  display: grid;
  text-align: center;
  border-bottom: 1px solid gray;
  grid-template-columns: 1fr 1fr 1fr;
}

.top > .btn {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 20px;
  font-size: 12px;
  width: 50px;
  height: 50px;
  font-weight: bold;
  border-radius: 12px;
}

.nav {
  height: 1000px;
  border-right: 1px solid gray;
  font-size: 24px;
}

.nav > ol {
  margin-bottom: 0;
}

.article {
  margin-left: 20px;
}

.btn {
  border: 0;
  padding: 0;
  text-align: center;
  background-color: #00000000;
}

.nav > .btn {
  font-size: 24px;
  margin-left: 40px;
}
